/**
 * WordPress dependencies.
 */
import { Component, Fragment } from '@wordpress/element';
import { compose } from '@wordpress/compose';
import { withDispatch } from '@wordpress/data';
import { __ } from '@wordpress/i18n';
import { Icon } from '@wordpress/components';


import './editor.scss';

/**
 * Inner dependencies.
 */
import TemplatesModel from './plugin-setting-sidebar/template-model.js';

class edit extends Component {
	constructor( props ) {
		super( props );
		// bind function
		this.getLayoutsSelector = this.getLayoutsSelector.bind( this );

		this.state = {
			isModelOpen: false,
		};
	}
	getLayoutsSelector() {
		const {
			clientId,
			attributes,
			removeBlock,
		} = this.props;
		return (
			<>
				{ attributes.isTemplatesModalOnly || this.state.isTemplatesModalOpen ? (
					<TemplatesModel
						onRequestClose={ () => {
							this.setState( {
								isTemplatesModalOpen: false,
							} );
							if ( attributes.isTemplatesModalOnly ) {
								removeBlock( clientId );
							}
						} }
						replaceBlockId={ clientId }
					/>
				) : (
					''
				) }
			</>
		);
	}
	render() {
		const {
			className,
		} = this.props;
		const openModal = () => {
			this.setState( {
				isTemplatesModalOpen: true,
			} );
		};
		return (
			<div className="wptravel-import-template">
				{ this.getLayoutsSelector() }
				<button onClick={ () => openModal() }><Icon icon="admin-appearance" /> { __( 'Browse Templates' ) }</button>
			</div>
		);
	}
}

export default compose( [
	withDispatch( ( dispatch ) => {
		const { updateBlockAttributes, removeBlock, replaceInnerBlocks } = dispatch(
			'core/block-editor'
		);

		return {
			updateBlockAttributes,
			removeBlock,
			replaceInnerBlocks,
		};
	} ),
] )( edit );
